  },
  onReady() {

  },
  methods: {
    onLaunchMp(isQQ) {
      console.log('[launchMiniProgramInGame]');

      launchMiniProgramInGame({
        appId: 'wx99c1c10a389e7433',
        isWxMp: !isQQ,
      });

    },
    onClick(event) {
      console.log('[CLICK]', event);
    },
    onVibrateLong() {
      if (isNotInUni()) return;

      uni.vibrateLong({
        success() {
          console.log('success');
        },
        fail() {
          console.log('fail');
        },
      });
    },
    onVibrateShort() {

      navigator.vibrate(1000);


      if (isNotInUni()) return;

      uni.vibrateLong({
        success() {
          console.log('success');
        },
        fail() {
          console.log('fail');
        },
      });
    },
  },
};
